      saveSetting(val){
        var self=this;
        var url=self.baseUrl+"passRuleTime/setTime";
           //判断，获取星期;
           var whatWeek=[];
        if(val.checked1Val){
          whatWeek.push(1)
        }
        
        if(val.checked2Val){
