        if(val.checked7Val){
          whatWeek.push(7);
        }
        whatWeek=whatWeek.join(",");
        // console.log(self.judgeVal(self.setTimeForm.startDate))
        // console.log(self.judgeVal(self.setTimeForm.endDate))

        // console.log(self.setTimeForm.dataObj.length);
        // console.log(self.setTimeForm.weekObj.length);
        // console.log(self.setTimeForm.jqObj.length);

        var passRuleDate=[],passRuleHoliday=[],passRuleWeek=[];
        //遍历日期
        for(var i=0;i<self.setTimeForm.dataObj.length;i++){
          passRuleDate[i]={
            'startDate':self.momentTime1(self.setTimeForm.startDate),
            'endDate':self.momentTime1(self.setTimeForm.endDate),
            "startTime":self.momentTime2(self.setTimeForm.dataObj[i].startTime),
            "endTime":self.momentTime2(self.setTimeForm.dataObj[i].endTime),
          }
        }
        
        //遍历假期
        for(var i=0;i<self.setTimeForm.jqObj.length;i++){
          passRuleHoliday[i]={
            "holidayName":self.setTimeForm.holidayName,
            "startTime":self.momentTime(self.setTimeForm.jqObj.holidayStartTime),
            "endTime":self.momentTime(self.setTimeForm.jqObj.holidayEndTime),
          }
        }
        //遍历周期
        for(var i=0;i<self.setTimeForm.weekObj.length;i++){
          passRuleWeek[i]={
            "whatWeek":whatWeek,
            "startTime":self.momentTime2(self.setTimeForm.weekObj.weekStartTime),
            "endTime":self.momentTime2(self.setTimeForm.weekObj.weekEndTime),
          }
        }

        var params={
          "parkingId":self.setTimeForm.parkingId,
          "timeName":self.setTimeForm.timeName,
          "passRuleDate":passRuleDate,
          "passRuleHoliday":passRuleHoliday,
          "passRuleWeek":passRuleWeek,
        }
        self.$axios.post(url,params).then(function(res){
          console.log(res.data.message)
         if(res.data.status=='0'){
          self.$message({
            message:res.data.message,
            type:"success"
          })
         }else{
          self.$message({
            message:res.data.message,
            type:"info"
          })
         }
          self.dialogVisibleAdd=false;
        }).catch(err=>console.log(err));
