         self.detailForm.startTime='Mon Apr 22 2019 '+res.data.data.passRuleDate[0].startTime+" GMT+0800"
         self.detailForm.endTime='Mon Apr 22 2019 '+res.data.data.passRuleDate[0].endTime+" GMT+0800"
         //假期
         self.detailForm.holidayName=res.data.data.passRuleHoliday[0].holidayName;
          self.detailForm.holidayStartTime=res.data.data.passRuleHoliday[0].startTime
          self.detailForm.holidayEndTime=res.data.data.passRuleHoliday[0].endTime
          // 星期
          self.detailForm.weekStartTime=res.data.data.passRuleWeek[0].startTime;
          self.detailForm.weekEndTime=res.data.data.passRuleWeek[0].endTime;
          var weekARR=res.data.data.passRuleWeek[0].whatWeek.split(",");
                  if(weekARR.length>0){
                    for(var i=0;i<weekARR.length;i++){
                        if(weekARR[i]=='1'){
